import Artyom from './node_modules/artyom.js/build/artyom.js';

export default class ArtyomAssistant {
  constructor() {
      this.artyom = new Artyom();
      this.isListening = false;
  }

  startListening() {
      this.artyom.fatality(); // Reset Artyom
      setTimeout(() => {
          this.artyom.initialize({
              lang: "en_US",
              continuous: true,
              listen: true,
              debug: true,
              speed: 1,
          }).then(() => {
              console.log("Artyom is ready to listen!");
              this.isListening = true;
          }).catch(err => {
              console.error("Initialization error:", err);
              alert("Artyom initialization failed. Please check your settings.");
          });

          // Redirect recognized text output to sidebar
          this.artyom.redirectRecognizedTextOutput((recognized, isFinal) => {
              const recognizedTextDiv = document.getElementById("recognizedText");
              recognizedTextDiv.textContent = isFinal
                  ? "Final recognized text: " + recognized
                  : recognized;
          });
      }, 250);
  }

  stopListening() {
      this.artyom.fatality();
      console.log("Artyom has stopped listening.");
      this.isListening = false;
  }

  toggleListening() {
      if (this.isListening) {
          this.stopListening();
      } else {
          this.startListening();
      }
  }
}